          <p>Wahoo ELEMNT BOLT GPS&nbsp;&mdash; это велокомпьютер, который позволяет оптимизировать свои велотренировки, сделав их&nbsp;максимально эффективными. Wahoo ELEMNT BOLT GPS синхронизируется с&nbsp;датчиками по&nbsp;ANT+, объединяя полученную с&nbsp;них информацию. Данные отображаются на&nbsp;дисплее, а&nbsp;также сохраняются на&nbsp;смартфоне. При этом на&nbsp;мобильное устройство можно установить как фирменное приложение, так и&nbsp;различные приложения сторонних разработчиков. Велокомпьютер точно отслеживает местоположение, принимая сигнал с&nbsp;целого комплекса спутников. Эта информация позволяет смотреть уже преодоленные маршруты и&nbsp;планировать новые велопрогулки.</p>

