<script>
// import categories from '@/data/categories';
import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';
import chengeAmountProductVue from '@/components/chengeAmountProduct.vue';
import { mapActions } from 'vuex';
import axios from 'axios';
import API_BASE_URL from '../config';

export default {
  data() {
    return {
      productDate: null,
      productLoading: false,
      productLoadingFail: true,
      productAdded: false,
      productAddSending: false,
      productAmount: 1,
    };
  },
  filters: {
    numberFormat,
  },
  computed: {
    product() {
      return this.productDate;
    },
    category() {
      return this.productDate.category;
    },
    amount() {
      return this.productAmount;
    },
  },
  methods: {
    ...mapActions(['addProducttoCart']),
    gotoPage,
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;
      this.addProducttoCart({ productId: this.product.id, amount: this.productAmount })
        .then(() => {
          this.productAdded = true;
          this.productAddSending = false;
        });
    },
    increment() {
      this.$emit('productAmount', this.productAmount + 1);
    },
    loadProduct() {
      this.productLoading = true;
      this.productLoadingFail = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios.get(`${API_BASE_URL}/api/products/${+this.$route.params.id}`)
          .then((response) => {
            this.productDate = response.data;
            return this.productDate;
          })
          .catch(() => {
            this.productLoadingFail = true;
            return this.productLoadingFail;
          })
          .then(() => {
            this.productLoading = false;
            return this.productLoading;
          });
      }, 0);
    },
  },
  watch: {
    '$route.params.id': {
      handler() {
        this.loadProduct();
      },
      immediate: true,
    },
  },
  components: {
    chengeAmountProductVue,
  },
};
</script>
