<template>
    <!-- eslint-disable max-len -->
  <!-- eslint-disable -->
    <div class="form__counter">
      <button type="button" style="cursor: pointer;" aria-label="Убрать один товар" @click="decrement">
        <svg width="12" height="12" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <label for="count">
        <input type="text" v-model.number="productAmount" name="count" @input="$emit('update:productAmount', $event.target.value)">
      </label>
      <button type="button" style="cursor: pointer;" aria-label="Добавить один товар" @click="increment">
        <svg width="12" height="12" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>
</template>

<script>
export default {
  props: ['productAmount'],
  methods: {
    amount() {
      this.$emit('productAmount', this.productAmount);
    },
    increment() {
      this.$emit('update:productAmount', this.productAmount + 1);
    },
    decrement() {
      if (this.productAmount > 1) {
        this.$emit('update:productAmount', this.productAmount - 1);
      }
    },
  },
  computed: {
    currentAmount() {
      return this.productAmount;
    },
  },
};
</script>
