<template>
  <!-- eslint-disable max-len -->
  <!-- eslint-disable -->
  <li class="cart__item product" >
    <div class="product__pic">
      <img :src="product.product.image" width="120" height="120" :alt="product.product.title">
    </div>
    <h3 class="product__title">
      {{ product.product.title }}
    </h3>
    <span class="product__code">
      Артикул: {{ product.product.id }}
    </span>

    <div class="product__counter form__counter">
      <chengeAmountProductVue :product-amount.sync="amount"/>
    </div>

    <b class="product__price">
      {{ product.amount * product.product.price | numberFormat }} ₽
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="removeFromCart(product.productId)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapMutations, mapActions } from 'vuex';
import chengeAmountProductVue from '@/components/chengeAmountProduct.vue';

export default {
  props: ['product'],
  components: {
    chengeAmountProductVue,
  },
  filters: {
    numberFormat,
  },
  computed: {
    amount: {
      get() {
        return this.product.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', { productId: this.product.productId, amount: value });
      },
    },
  },
  methods: {
    ...mapMutations({ deleteProduct: 'deleteProduct', incrementAmoumt: 'incrementAmoumt' }),
    ...mapActions({ deleteProduct: 'deleteProduct' }),
    removeFromCart(prodId) {
      this.deleteProduct({ productId: prodId }).then(() => {
      });
    },
  },
};
</script>
